import back from '@/components/back'

export default {
    components:{
        back
    },
    data(){
        return{
            info: null,
            id: null,
            rejectId: null,
            reason: [],
            RejectReasonIds: [],
            user: this.$store.state.userInfo.Data,
            showConfirm: false,
            medList: [],
			DrType:0
        }
    },
    created(){
        this.pull()
        this.pullReject()
    },
    methods: {
        pull(){
            this.medList = []
            this.id=this.$route.query.id
            this.rejectId=this.$route.query.rejectId
            if(this.id){
                this.$post('/Prescription/Prescription/GetPrescription',{
                    id:this.id,
                }).then(o => {
                    this.info = o
                    this.RejectReasonIds = o.RejectReasonId ? o.RejectReasonId.split(',') : []
                    let length = o.ListDetails.length / 5
                    if(length > 1){
                        for(let i = 0; i<length; i++){
                            this.medList.push(o.ListDetails.slice(i*5,(i+1)*5))
                        }
                    }
                    else{
                        this.medList[0] = o.ListDetails
                    }
                }) 
            }
            if(this.rejectId){
                this.$post('/Prescription/Prescription/GetPrescription',{
                    id:this.rejectId,
                }).then(o => {
                    this.info = o
                    this.RejectReasonIds = o.RejectReasonId ? o.RejectReasonId.split(',') : []
                    const obj = {}
                    obj.PatientInfo = {
                        PatientId: o.PatientId,
                        PatientName: o.PatientName,
                        PatientSex: o.PatientSex,
                        Birthday: o.Birthday,
                        Age: o.Age,
                    },
                    obj.DoctorPatientRelation = {
                        DrPatientId: o.DrPatientId,
                    }
                    this.$store.dispatch('pushpInfo',obj)
                    this.$store.dispatch('pushpatient',obj.PatientInfo)
                }) 
            }
			this.$get('PlatFormAPI/Doctor/QueryDoctorInfoByDrId',{
			    DrId: this.user.User.DoctorId
			}).then(o => {
				 this.DrType = o.DrType
			})
        },
        withdraw(){
            this.$post('Prescription/Prescription/RevokePrescription',{
                PrescriptionId: this.$route.query.id,
                DoctorId: this.user.User.DoctorId
            }).then(o => {
                this.$toast('撤回成功')
                this.pull()
            })
        },
        pullReject(){
        	//西药驳回的理由
            this.$get('PlatFormAPI/Category/QueryItemByCategoryId',{
                CategoryId: 37
            }).then(o => {
                this.reason = o
            })
        },
        toReset(){
            this.$store.dispatch('pushDiagnoses2', null)
            this.$store.dispatch('pushupList', [])
            this.$store.dispatch('pushpatient',{
                PatientName: this.info.PatientName,
                PatientSex: this.info.PatientSex,
                PatientId: this.info.PatientId,
                Age: this.info.Age,
            })
            this.$store.dispatch('pushpInfo',{
                ApplicantName: this.info.ApplicantName,
                ApplicantId: this.info.ApplicantId,
                PatientInfo:{
                    PatientName: this.info.PatientName,
                    PatientSex: this.info.PatientSex,
                    PatientId: this.info.PatientId,
                    Age: this.info.Age,
                }
            })
            this.$router.push('/pharmacy?from=p')
        },
        modify(){
            this.$store.dispatch('pushpullFlag', 0)
            this.$store.dispatch('pushpatient',{
                PatientName: this.info.PatientName,
                PatientSex: this.info.PatientSex,
                PatientId: this.info.PatientId,
                Age: this.info.Age,
            })
            this.$store.dispatch('pushpInfo',{
                ApplicantName: this.info.ApplicantName,
                ApplicantId: this.info.ApplicantId,
                PatientInfo:{
                    PatientName: this.info.PatientName,
                    PatientSex: this.info.PatientSex,
                    PatientId: this.info.PatientId,
                    Age: this.info.Age,
                }
            })
            this.$router.push(`/prescription?rejectId=${this.info.Id}`)
        },
        reject(){
            this.$bridge.callhandler('preReject')
        },
        accept(){
            this.$bridge.callhandler('preAccept')
        }
    }
}



